*{
   margin: 0;
   padding: 0;
   font-family: Jameel Noori Nastaleeq;
 }
  .mainLoop
  {
    direction: rtl;
    display: grid;
    grid-template-columns: 27% 73%;
    border: solid 2px black;
    border-radius: 10px;
  }
  .surahIntro
  {
    border: solid 2px black;
    border-radius: 10px;
    margin: 2%;
    padding: 30px;
  }
  .id
  {
    display: grid;
    grid-template-columns: 20% 70%;
    border: solid 2px #111;
    border-radius: 10px;
    margin: 3%;
  }
  .id:hover
  {
      background-color: #485460;
      color: #fff;
  }
  .surahName
  {
   border: solid 2px black;
   margin: 18px;
   border-radius: 10px;
  }
   .ainkerTag
   {
    text-decoration: none;
   }
